'use client';

import { useEffect, useState } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { Loader2 } from 'lucide-react';
import { useAuth } from '@/providers/auth-provider';
import { Button } from '@/components/ui/button';

export default function ProtectedRoute({ children }: { children: React.ReactNode }) {
  const { isAuthenticated, isLoading } = useAuth();
  const router = useRouter();
  const pathname = usePathname();
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  // Si no estamos en el cliente o estamos cargando, mostrar un indicador de carga
  if (!isClient || isLoading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  // Si no está autenticado, el middleware ya debería haber redirigido al login
  // Pero por si acaso, mostramos un mensaje de error
  if (!isAuthenticated) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <h2 className="text-xl font-semibold mb-2">No autorizado</h2>
          <p className="text-muted-foreground mb-4">Por favor inicia sesión para continuar</p>
          <Button onClick={() => router.push('/login')}>
            Ir al inicio de sesión
          </Button>
        </div>
      </div>
    );
  }

  return <>{children}</>;
}
